<template>
  <div>

    <div v-if="this.$route.query.status!=1">
      <van-nav-bar
      fixed
      @click-left="quit"
      :title="'隐私政策'"
      left-text="返回"
      left-arrow
   
    >
    </van-nav-bar    >

    <div class="content" v-if="this.$route.query.status!=1">
      
      <span>
        请你务必审慎阅读，充分理解“隐私政策”各条款，包括但不限于：为了向你提供拍照、OCR等个人信息。你可以在设置中查看、变更、删除个人信息并管理你的授权。
      </span>
    </div>
</div>


<div  v-if="this.$route.query.status==1">


  <van-nav-bar  @click-left="quit" title="修改密码" left-arrow />




<van-cell :title="$store.getters.userName"></van-cell>
<van-field v-model="oldPassword" type="password" label="旧密码" placeholder="请输入旧密码" />
<van-field v-model="newPassword" type="password" label="新密码"  placeholder="请输入新密码" />
<van-field v-model="newPasswords" type="password" label="确认新密码"  placeholder="请输入新密码" />


  <div class="login">
     <div class="con" >


        <div
          class="con_04"
          style="

            display: flex;
            justify-content: space-between;
          "
        >
          <div class="con_04_1 con_04_3" @click="edit_psd">确定修改</div>

        </div>
      </div>

  </div>

</div>

</div>
</template>

<script>
import { Police } from "@/api/login";
export default {
  name:"homepage",
  data() {
    return {


      activeKey: 0,
      userName: "",
      newPassword: "",
      newPasswords: "",
      oldPassword: "",
     
      code: "",
      status: 0,
      psd: "",
      newPass: "",
      verifyPass: "", //确认密码

      username: "",
      password: "",
      ischecked: true, //是否记住密码 默认记住密码
      isAgree: true, //是否同意协议
      checked: true,
    };
  },
  created() {
    this.status = this.$route.query.status;
    //console.log(this.$route.query.status);
    
  },
  methods: {
    quit(){
     this.$root.$emit('home',{
             firstPlayFlag:0
          });
      this.$router.back()
    },

    
    edit_psd() {
      if(this.oldPassword==""){
         this.$api.msg('请输入旧密码');
      }else if(this.newPassword==""){
         this.$api.msg('请输入新密码');
      }else if(this.newPasswords==""){
         this.$api.msg('请再次确认新密码');
      }else{
             Police({
               new_password: this.newPassword,
               old_password: this.oldPassword,
             }).then((res) => {
               if (res.code == 0) {
                 this.$api.msg("修改成功");
                 this.$router.push({
                   path: "/login",
                 });
               } else {
                 this.$api.msg(res.msg);
               }
               //console.log(res);
             });
             }
    },


    back() {
      // uni.navigateBack()
      this.status = 0;
    },
    change_sta() {
      this.status = 1;
    },

  }
};
</script>

